import Typography from '@mui/joy/Typography';
import Sheet from '@mui/joy/Sheet';

export function Dashboard({ searchParams, setSearchParams, go }) {
    return (
        <Sheet sx={{
            display: "flex",
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            alignContent: 'center',
            minHeight: '100vh',
            overflow: 'hidden',
            fontFamily: 'Inter',
        }}>
            <Typography level='h1'>Hello</Typography>
        </Sheet>
    );
}